html, body {
    font: 14px/1.21 'Helvetica Neue', arial, sans-serif;
    font-weight: 400;
}

h1 {
    text-align: center;
}

.logoWrapper {
    margin-bottom: 50px;
}

.logo {
    width: 100px;
    height: auto;
}

.open-btn {
    display: inline-block;
    padding: 10px;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 4px;
    color: #222;
    text-decoration: none;
    padding-left: 40px;
    position: relative;
    margin-bottom: 20px
}

.open-btn:before {
    content: '';
    position: absolute;
    left: 5px;
    top: 5px;
    width: 30px;
    height: 30px;
    background: url('./assets/logo.png') no-repeat;
    background-size: 25px;
}



