.btn {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: inline-block;
    text-align: left;
    white-space: nowrap;
    text-decoration: none;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid #ddd;
    padding: 4px 8px;
    margin: 5px auto;
    border-radius: 5px;
    color: #fff;
    fill: #fff;
    background: #000;
    line-height: 1em;
    min-width: 190px;
    height: 45px;
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
    -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    font-family: 'San Francisco';
    font-weight: 500;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -moz-font-feature-settings: 'liga', 'kern';
}

.btn:hover,
.btn:focus {
    background: #111;
    color: #fff;
    fill: #fff;
    border-color: #fff;
    -webkit-transform: scale(1.01) translate3d(0, -1px, 0);
    transform: scale(1.01) translate3d(0, -1px, 0);
    -webkit-box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.btn:active {
    outline: 0;
    background: #353535;
    -webkit-transition: none;
    transition: none;
}

.btn__icon,
.btn__text,
.btn__storename {
    display: inline-block;
    vertical-align: top;
}

.btn__icon {
    width: 30px;
    height: 30px;
    margin-right: 5px;
    margin-top: 2px;
}

.btn__icon--amazon {
    -webkit-transform: scale(0.85);
    transform: scale(0.85);
}

.btn__text {
    letter-spacing: 0.08em;
    margin-top: -0.1em;
    font-size: 10px;
}

.btn__storename {
    display: block;
    margin-left: 38px;
    margin-top: -17px;
    font-size: 22px;
    letter-spacing: -0.03em;
}

.btn--small {
    padding: 2px 8px;
    min-width: 118.75px;
    height: 24px;
    border-radius: 3px;
}

.btn--small .btn__icon {
    width: 16px;
    height: 16px;
    margin: 1px 2px 0 0;
}

.btn--small .btn__text {
    display: none;
}

.btn--small .btn__storename {
    font-size: 12px;
    display: inline-block;
    margin: 0;
    vertical-align: middle;
}

.btn--tiny {
    padding: 3px;
    width: 22px;
    height: 22px;
    min-width: 0;
    border-radius: 3px;
}

.btn--tiny .btn__icon {
    width: 14px;
    height: 14px;
    margin: 0;
}

.btn--tiny .btn__text,
.btn--tiny .btn__storename {
    display: none;
}

html,
body {
    width: 100%;
    height: 100%;
}

body {
    margin: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0 10%;
    max-width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    text-align: center;
}

@font-face {
    font-family: "San Francisco";
    font-weight: 500;
    src: url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-semibold-webfont.woff2"), url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-semibold-webfont.woff") format('woff'), url("https://applesocial.s3.amazonaws.com/assets/styles/fonts/sanfrancisco/sanfranciscodisplay-semibold-webfont.ttf") format('truetype');
}



